.navbar {
  width: 100%;
  height: 5%;
  position: fixed;
  top: 0;
  z-index: 100; /* This will ensure your navbar stays on top */
}
header {
  padding-top: 4rem; /* Adjust this value as needed, 4rem is just an example */
  height: 100vh;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.header__container {
  top: 50rem;
  left: 2rem;
  text-align: center;
  height: 100%;
  position: relative;
}

/* =============== video ============== */
video{
  object-fit: cover;
  position: absolute;
  top: 0;
  left:0;
  right: 0;
  width: 100%;
  height: 100%;
}

/* =============== CTA ============== */
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* =============== HEADER SOCIALS ============== */
.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10rem;
  position: fixed;
  left: 5rem;
  bottom: 25rem;

}

.header__socials::after {
  content: "";
  width: 2px;
  height: 2rem;
  background: var(--color-primary);
}

/* =============== ME ============== */
.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* =============== SCROLL DOWN ============== */
.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/* ================ MEDIA QUERIES (MEDIUM DEVICES) ================= */
@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}

/* ================ MEDIA QUERIES (SMALL DEVICES) ================= */
@media screen and (max-width: 600px) {
  header {
    height: 100vh;
    /* added after tutorial */
    padding-top: 4rem;
  }

  .header__socials,
  .scroll__down {
    display: none;
  }

  /* added after tutorial */
  .me {
    width: 94%;
    max-width: 20rem;
    margin-top: 2.5rem;
    justify-content: center;
  }

  /* added after tutorial */
  .cta {
    margin-top: 1.7rem;
    gap: 1rem;
  }
}
